import React from 'react';
import ScrollAnimation from 'react-animate-on-scroll';

function Portfolio () {
   return (
      <section id="_portfolio" className="portfolio">
         <div className="container panel">

            <div className="section-name">
               <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                  <div className="bar"></div>
               </ScrollAnimation>
               <ScrollAnimation animateIn="open-anim" animateOut="close-anim" offset={100}>
                  <h3>PORTFOLIO</h3>
               </ScrollAnimation>
               <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                  <div className="bar"></div>
               </ScrollAnimation>
            </div>

            <ul className="projects">
               <li className="project-1">
                  <a href="http://creationbasin.com/#/Ichigo"><div className="img"></div></a>
                  <a className="title" href="http://creationbasin.com/#/Ichigo">Creation Basin</a>
                  <a className="github" href="https://github.com/jemcafe/personal-project">Github</a>
               </li>
               <li className="project-2">
                  <a href="http://woofwalks.us/#/"><div className="img"></div></a>
                  <a className="title" href="http://woofwalks.us/#/">Woof Walks</a>
                  <a className="github" href="https://github.com/tcb1978/woofWoofMeow">Github</a>
               </li>
               <li className="project-3">
                  <a href="https://jemcafe.github.io/calendar/"><div className="img"></div></a>
                  <a className="title" href="https://jemcafe.github.io/calendar/">Calendar</a>
                  <a className="github" href="https://github.com/jemcafe/calendar">Github</a>
               </li>
            </ul>

         </div>
      </section>
   );
}

export default Portfolio;


