import React from 'react';
import ScrollAnimation from 'react-animate-on-scroll';

function About () {
   return (
      <section id="_about" className="about">
         <div className="container panel">

            <div className="section-name">
               <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                  <div className="bar"></div>
               </ScrollAnimation>
               <ScrollAnimation animateIn="open-anim" animateOut="close-anim" offset={100}>
                  <h3>ABOUT</h3>
               </ScrollAnimation>
               <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                  <div className="bar"></div>
               </ScrollAnimation>
            </div>

            <p>I am a full stack developer with a design background. Below are some the tools I use to create sites like this one.</p>
               
         </div>
      </section>
   );
}

export default About;


